import { m } from "framer-motion";
import { useEffect, useState } from "react";
// @mui
import { Box, Grid, Typography } from "@mui/material";
import Image from "../Image";
// components
import useLocales from "../../hooks/useLocales";
import { MotionViewport, varFade, varZoom } from "../animate";
import DefaultBtn from "../DefaultBtn";

// ----------------------------------------------------------------------

export default function Book() {
  const { translate } = useLocales();
  // const bookData = [
  //   { img: "/assets/images/book_img.png", title: "book_section_title1" },
  //   { img: "/assets/images/book_img.png", title: "book_section_title2" },
  //   { img: "/assets/images/book_img.png", title: "book_section_title3" },
  //   { img: "/assets/images/book_img.png", title: "book_section_title4" },
  // ];
  const [bookData, setBookData] = useState([]);

  const fetchData = () => {
    return fetch("https://backoffice.lobreauserge.com/api/books")
      .then(
        (response) => response.json()
      )
      .then(
        (data) => setBookData(data)
      );
  }
  useEffect(() => {
    fetchData();
  }, [])
  const amazonBooks = ["https://www.amazon.com/dp/B0CHGGC2SN/ref=pe_386300_440135490_TE_simp_item_image?nodl=1&dplnkId=9f17bf1c-6b80-446b-9593-3e662e272a9f"]
  return (
    <MotionViewport>
      <Box px={3} pt={{ xs: 6, md: 10 }} id="book">
        <Box maxWidth="855px" sx={{ mx: "auto" }}>
          <m.div variants={varFade({ durationIn: 1.2 }).inDown}>
            <Typography
              variant="h1"
              mb={4}
              textTransform="capitalize"
              textAlign="center"
            >
              {translate("book_section_title")}
            </Typography>
          </m.div>
          <Grid container spacing={4} justifyContent="center">
            {bookData.map((item, index) => (
              <Grid item key={index} xs={12} sm={6} md={3} lg>
                <m.div variants={varZoom({ durationIn: 1 + index / 5 }).in} style={{ height: "100%" }}>
                  <Box
                    component="a"
                    py={2}
                    px={2}
                    display="flex"
                    flexDirection="column"
                    alignItems="center"
                    sx={{
                      // background:
                      //   "linear-gradient(180deg, rgba(16, 19, 34, 0) 0%, rgba(116, 90, 203, 0.21) 111.47%)",
                      maxWidth:"260px",
                      margin:"0 auto",
                      height: "100%",
                      border: "1px solid #181930",
                      borderRadius: "16px",
                    }}
                    href={amazonBooks[index]}
                    target="_blank"
                  >
                    <Image src={item.image} sx={{ width: "100%", height: "100%" }} />
                    {/* <Typography
                      variant="body1"
                      dangerouslySetInnerHTML={{
                        __html: translate(item.title),
                      }}
                    /> */}
                  </Box>
                </m.div>
              </Grid>
            ))}
          </Grid>
          <m.div variants={varFade({ durationIn: 1.4 }).inDown}>
            <Box mt={{ xs: 3, sm: 5 }} textAlign="center">
              <DefaultBtn
                text="view_all"
                icon="heroicons:arrow-long-right"
                href="#"
              />
            </Box>
          </m.div>
        </Box>
      </Box>
    </MotionViewport>
  );
}
