
import Particles from 'react-particles-js';


export default function Particle() {
	const params = {
    particles: {
      number: {
        value: 120
      },
      size: {
        value: 3
      }
    },
    interactivity: {
      events: {
        onhover: {
          enable: true,
          mode: 'repulse'
        }
      }
    }
  };
  return (
    <>
      <Particles className="particles" params={params} />
    </>
  );
}
